
        import {store, MERGED_BUNDLES_NAME} from 'bundleStore';
        
        let bundle;
        
        if ('GeneralSettingsBundle' !== MERGED_BUNDLES_NAME) {
            store['GeneralSettingsBundle'] = store['GeneralSettingsBundle'] || {};
            bundle = store['GeneralSettingsBundle'];
        } else {
            bundle = Object.keys(store).reduce((acc, key) => {
                return {
                    ...acc,
                    ...store[key]
                }
            }, {});
        }
                                     
        export default bundle;
    