// @ts-ignore
import Administer from '../../administer/administer.base';
// @ts-ignore
import general from '../../administer/administer.general';

const showHideDriverJarUpload = (value: boolean, flowExecution: string) => {
    const params = {
        name: 'allowJarFileUpload',
        value,
        _flowExecutionKey: flowExecution,
        _eventId: 'saveSingleProperty'
    }
    const url = `flow.html?${(<any>Object).toQueryString(params)}`
    Administer._sendRequest(url, params, general._updateCallback);
}
export default showHideDriverJarUpload;
